@keyframes fadeout {
    from {
        margin: 0;
        opacity: 1;
    }
    to {
        margin: 200px;
        opacity: 0;
    }
}

@keyframes fadein {
    from {
        margin: -200px;
        opacity: 0;
    }
    to {
        margin: 0;
        opacity: 1;
    }    
}

.dissapear{
    animation-name: fadeout;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    z-index: 1000;
    position: fixed;
}

.appear{
    animation-name: fadein;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    z-index: 1001;
    position: fixed;
}

.cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10em;
    grid-row-gap: 5em;
    padding: 10em;
}

.cards > *{
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid #555;
    box-shadow: 0 0 20px #000;
    transition: all 0.3s;
    height: 12em;
}

.cards > *:hover {
    margin: -10px;
}

.cards > * > *:nth-child(2){
    flex-grow: 1;
}

.cards > * > *:nth-child(3){
    width: 100%;
}

button {
    border: 1px solid #555;
    background-color: #0000;
    padding: 10px;
    width: 100%;
}

button:hover{
    background-color: #777;
}
